import Vue from 'vue'

const state = () => ({
	items: [],
	user_name: ''
})

const mutations = {
	'SET_ITEMS': (state, payload) => {
		Vue.set(state, 'items', payload)
	},
	'SET_USERNAME': (state, value) => {
		Vue.set(state, 'user_name', value)
	}
}

const actions = {
	setItems ({commit}, payload) {
		commit('SET_ITEMS', payload)
	},
	setUsername ({commit}, value) {
		commit('SET_USERNAME', value)
	}
}

const getters = {
	getItems: state => { return state.items },
	getUsername: state => { return state.user_name }
}

export default {
	state,
	getters,
	mutations,
	actions
}



