<template>
	<div :class="{'mobile-container': isMobile}">
		<div class="party-list" :style="getMarginTop">
			<div :style="getPartyListStyle" class="text-xs-center mb-3">
				<!-- <v-text-field
					solo
					class="type-input"
					v-model="partyType"
					:placeholder="getHeaderMessage"
					@keyup.enter="enterPartyType"
				>
				</v-text-field> -->
				<h1 class="text-xs-center" style="display: inline;">&nbsp;{{getHeaderMessage}}<span :style="getCaretStyle">|</span></h1>
				<h1>Checklist</h1>
			</div>
			<div :style="getPartyListStyle">
				<div v-for="(item, index) in getItems" class="item">
					<p class="item-name" style="padding: 8px 16px 8px 16px;">{{item.name}}</p>
					<v-text-field
						:id="'bringer_' + index" 
						solo 
						flat
						v-show="bringIndex === index" 
						class="name-input editing-item"
						@keyup.enter="finish(index)"
						placeholder="Add your name"
						v-model="tempBringer"
						@blur="blurFinish(index)"
						style="padding: 1px 0 1px 0;"
					>		
					</v-text-field>
					<div v-if="bringIndex !== index" style="display: flex; flex-direction: row; justify-content: flex-end; align-items: center;">
						<p v-if="!isMobile" class="ma-0 bringer-name">{{item.bringer}}</p>
						<v-btn @click="uncheck(index)" v-if="getItems[index].bringer !== ''" flat class="checkbox-button" :style="getCheckboxStyle(index)" :disabled="getCheckboxDisabled(index)"><i class="far fa-check-square checkbox-icon"></i></v-btn>
						<v-btn @click="check(index)" v-else flat class="checkbox-button unchecked"><i class="far fa-square checkbox-icon"></i></v-btn>
					</div>
					<!-- <div style="width: 50px;">
						<v-checkbox v-model="tempItemsChecked[index]" @change="checkItem(index)" hide-details class="item-checkbox" label="">
						</v-checkbox>
					</div> -->
				</div>
			</div>
			<!-- <div style="display: flex; flex-direction: column; align-items: center;">
				<h1>What do we need?</h1>
				<div class="divider"></div>
				<div v-for="(item, index) in getItems" style="width: 100%;" class="item">
					<p class="item-name" style="padding: 8px 16px 8px 16px;">{{item.name}}</p>
				</div>
			</div>
			<div style="display: flex; flex-direction: column; align-items: center;">
				<h1>Who's bringing it?</h1>
				<div class="divider"></div>
				<div v-for="(item, index) in getItems" style="width: 100%;" class="item">
					<v-text-field
						:id="'bringer_' + index" 
						solo 
						v-show="bringIndex === index" 
						class="mt-2 mb-2"
						@keyup.enter="finish(index)"
						placeholder="Add your name"
						v-model="tempBringer"
					>		
					</v-text-field>
					<div v-if="bringIndex !== index" class="ma-0 pa-0">
						<div v-if="item.bringer !== ''" style="display: flex; flex-direction: row; justify-content: space-between; align-items: center;">
							<p class="item-name" style="padding: 8px 16px 8px 16px; flex-shrink: 20;">{{item.bringer}}</p>
							<v-btn @click="remove(index)" icon v-if="item.bringer === getUsername" class='icon-button--red'>
								<i class="far fa-times"></i>
							</v-btn>
						</div>
						<v-btn @click="bring(index)" block class="add-button" v-else><i style="font-size: 1.8em;" class="fal fa-gift"></i></v-btn>
					</div>
				</div>
			</div> -->
		</div>
	</div>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'

export default {
	data () {
		return {
			clickDown: false,
			editWait: false,
			headerMessages: [
				'Party',
				'Camping',
				'Shindig',
				'Hoedown',
				'Graduation',
				'Bar Mitsvah',
				'Hootenanny',
				'New Years',
				'Fourth of July'
			],
			headerMessageDirection: 1,
			headerMessageIndex: 0,
			currentMessageIndex: 0,
			caretVisible: false,
			tempItemsChecked: [
			],
			bringIndex: -1,
			tempBringer: ''
		}
	},
	mounted () {
		setTimeout(this.updateHeaderMessage, 500)
		setTimeout(this.flashCaret, 200)

		document.addEventListener('mousedown', () => {
			this.clickDown = true
		})
		document.addEventListener('mouseup', () => {
			this.clickDown = false
			setTimeout(() => {
				this.editWait = false
			}, 3)
		})
	},
	methods: {
		...mapActions([
			'setItems',
			'setUsername'
		]),
		check (index) {
			if (this.getUsername !== '') {
				let temp = Object.assign({}, this.getItems)
				temp[index].bringer = this.getUsername
				this.setItems(temp)
				this.tempBringer = ''
				this.bringIndex = -1
			} else {
				this.bringIndex = index
				setTimeout(() => {
					let box = document.getElementById('bringer_' + index)
					box.focus()
				}, 5)
			}
		},
		uncheck (index) {
			let temp = Object.assign({}, this.getItems)
			temp[index].bringer = ''
			this.setItems(temp)
			this.tempBringer = ''
			this.bringIndex = -1
		},
		bring (index) {
			if (!this.editWait) {
				if (this.getUsername !== '') {
					let temp = Object.assign({}, this.getItems)
					temp[index].bringer = this.getUsername
					this.setItems(temp)
					this.tempBringer = ''
					this.bringIndex = -1
				} else {
					this.bringIndex = index
					setTimeout(() => {
						let box = document.getElementById('bringer_' + index)
						box.focus()
					}, 5)
				}
			}
		},
		blurFinish (index) {
			this.finish(index)
			if (this.clickDown === true) {
				this.editWait = true
			}
		},
		finish (index) {
			if (this.bringIndex !== -1) {
				let temp = Object.assign({}, this.getItems)
				temp[index].bringer = this.tempBringer
				this.setUsername(this.tempBringer)
				this.setItems(temp)
				this.tempBringer = ''
				this.bringIndex = -1
			}
		},
		remove (index) {
			let temp = Object.assign({}, this.getItems)
			temp[index].bringer = ''
			this.setItems(temp)
			this.tempBringer = ''
			this.bringIndex = -1
		},
		updateHeaderMessage () {
			if (this.currentMessageIndex === 0 && this.headerMessageDirection === -1) {
				this.headerMessageIndex = ((this.headerMessageIndex + 1) % (this.headerMessages.length))
				this.headerMessageDirection = 1
			} else {
				if (this.headerMessageDirection === 1 && this.currentMessageIndex === this.headerMessages[this.headerMessageIndex].length) {
					this.headerMessageDirection = -1
					this.currentMessageIndex = this.headerMessages[this.headerMessageIndex].length
					setTimeout(this.updateHeaderMessage, 2000)
					return
				}
				this.currentMessageIndex += this.headerMessageDirection
			}
			setTimeout(this.updateHeaderMessage, 100)
		},
		flashCaret () {
			this.caretVisible = !this.caretVisible
			setTimeout(this.flashCaret, 600)
		},
		itemChecked (index) {
			if (this.getItems[index].bringer !== '') {
				return true
			}
		},
		checkItem (index) {
			console.log(index)
			let temp = Object.assign({}, this.getItems)
			temp[index].bringer = 'anon'
			this.setUsername(this.tempBringer)
			this.setItems(temp)
			this.tempBringer = ''
			this.bringIndex = -1
		},
		getCheckboxStyle (index) {
			if (this.getCheckboxDisabled(index)) {
				return {
					'color': '#A0B6F6!important'
				}
			} else {
				return {
					'color': '#3061F3!important'
				}
			}
		},
		getCheckboxDisabled (index) {
			if (this.getItems[index].bringer !== this.getUsername) {
				return true
			}
		}
	},
	computed: {
		...mapGetters([
			'getItems',
			'getUsername'
		]),
		getSize () {
			return this.$vuetify.breakpoint.name
		},
		getPartyListStyle () {
			let width = ''
			if (this.getSize === 'xl') {
				width = '60%'
			} else if (this.getSize === 'lg') {
				width = '70%'
			} else if (this.getSize === 'md') {
				width = '80%'
			} else if (this.getSize === 'sm') {
				width = '90%'
			} else {
				width = '100%'
			}
			return {
				'width': width
			}
		},
		getMarginTop () {
			let margin = ''
			if (this.getSize === 'xs') {
				margin = '8px 0 0 0'
			} else {
				margin = '46px 0 0 0'
			}
			return {
				'margin': margin
			}
		},
		getHeaderMessage () {
			return this.headerMessages[this.headerMessageIndex].substring(0, this.currentMessageIndex)
		},
		getCaretStyle () {
			if (this.currentMessageIndex === this.headerMessages[this.headerMessageIndex].length) {
				if (!this.caretVisible) {
					return {
						'color': 'transparent'
					}
				}
			}
		},
		isMobile () {
			if (this.$vuetify.breakpoint.name === 'sm' || this.$vuetify.breakpoint.name === 'xs') {
				return true
			} else {
				return false
			}
		}
	}
}
</script>
1
<style lang="scss" scoped>
.bringer-name {
	font-size: 1.3em;
	color: #888;
}
.type-input {
	width: 50%;
	margin-left: 25%;
	height: 50px;
	margin-bottom: 15px;
}
.blue-glow {
	box-shadow: 0px 2px 12px #69B4BE!important;
	outline: 1px solid #A0D6DE!important;
}
.checkbox-button {
	width: 40px!important;
	min-width: 40px!important;
	border-radius: 5px;
	&.unchecked {
		color: #333!important;
	}	
}

.checkbox-icon {
	font-size: 1.5em;
}
.item-checkbox {
	flex-shrink: 100;
	width: 50px!important;
	.input-group__input {
		width: 50px!important;
		min-width: 50px!important;
		flex-shrink: 100;
	}
}

.party-list {
	display: flex;
	flex-direction: column;
	align-items: center;
	.name-input {
		max-width: 50%;
	}
	.item-name {
		max-width: 60%;
	}
	h1 {
		margin-bottom: 10px;
		font-size: 2.5em;
		line-height: 1.2em;
		color: #444;
	}
}

.item {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	padding: 8px 0 8px 0;
	border-bottom: 1px solid #DDD;
}

.divider {
	height: 1px;
	width: 100%;
}

@media screen and (max-width: 600px) {
	.party-list {
		padding: 20px;
		h1 {
			font-size: 2.5em;
		}
	}
}
</style>


