<template>
	<v-app>
		<v-toolbar
			app
			flat
			absolute
			class="toolbar"
		>
			<v-btn @click="goHome" flat class="toolbar-title-button hidden-sm-and-down">
				<img height="30px" width="30px" class="mr-3" src="@/assets/baloons-brighter.png">
				<h1>Party Registry</h1>
			</v-btn>
			<div class="hidden-md-and-up">
				<v-btn flat class="toolbar-title-button hidden-sm-only ml-0" style="min-width: 0px!important width: 50px; height: 60px;">
					<img height="40px" width="40px" class="mt-0" src="@/assets/baloons.png">
				</v-btn>
				<v-btn flat class="toolbar-title-button hidden-xs-only" style="margin-top: 15px;">
					<img height="30px" width="30px" class="mr-3" src="@/assets/baloons.png">
					<h1>Party Registry</h1>
				</v-btn>
			</div>
			<v-spacer></v-spacer>
			<v-btn @click="menuOpen = !menuOpen" flat class="toolbar-menu-button hidden-sm-and-down mr-3">
				<h1>menu</h1>
			</v-btn>
			<div class="hidden-md-and-up mr-0">
				<v-btn flat large icon class="toolbar-menu-button-mobile hidden-xs-only mr-4 mt-4">
					<i class="fa fa-bars" style="font-size: 1.5em;"></i>
				</v-btn>
				<v-btn flat large icon class="toolbar-menu-button-mobile hidden-sm-only mr-4">
					<i class="fa fa-bars" style="font-size: 1.5em;"></i>
				</v-btn>
			</div>
		</v-toolbar>
		<v-content>
			<router-view/>
		</v-content>
		<v-footer fixed app class="footer mb-0" v-if="!isMobile">
			<v-spacer></v-spacer>
			<v-btn style="width: 50px; min-width: 50px;" @click="goToTwitter" flat class="footer-twiter-button">
				<!-- <h1>twitter</h1> -->
				<i style="color: white; font-size: 1.65em;" class="far fa-at"></i>
			</v-btn>
		</v-footer>
		<v-navigation-drawer right v-model="menuOpen" disable-resize-watcher app temporary>
			<v-btn flat @click="menuOpen = !menuOpen" class="close-button mr-3 mt-3" style="float: right;"><i style="font-size: 2.0em; color: #888;" class="fal fa-times"></i></v-btn>
		</v-navigation-drawer>
		<!-- Modals -->
		<share-party-modal></share-party-modal>
		<mobile-tutorial-modal></mobile-tutorial-modal>
	</v-app>
</template>

<script>
import SharePartyModal from './components/modals/SharePartyModal'
import MobileTutorialModal from './components/modals/MobileTutorialModal'
export default {
	components: {
		SharePartyModal,
		MobileTutorialModal
	},
	data () {
		return {
			title: 'Party Registry',
			menuOpen: false
		}
	},
	methods: {
		goToTwitter () {
			window.open('https://twitter.com/nickareias', '_blank')
		},
		goHome () {
			this.$router.push('/')
		}
	},
	computed: {
		isMobile () {
			if (this.$vuetify.breakpoint.name === 'xs' || this.$vuetify.breakpoint.name === 'sm') {
				return true
			}
		}
	},
	name: 'App'
}
</script>

<style lang="stylus">
  @require '../node_modules/vuetify/src/stylus/settings/_colors.styl'
  $theme := {
    primary: #009688
    accent: #FFC107
    secondary: #00796B
    info: #B2DFDB
    warning: $red.base
    error: #AAA
    success: $green.base
  }
  $body-font-family := 'Roboto', san-serif
  @require '../node_modules/vuetify/src/stylus/main.styl'
</style>

<style lang="scss">
.toolbar {
	background-color: transparent!important;

	.toolbar-title-button {
		background-color: transparent!important;
		h1 {
			font-size: 1.5em!important;
			color: #555!important;
			text-transform: capitalize!important;
		}
	}

	.toolbar-menu-button {
		background-color: transparent!important;
		h1 {
			font-size: 1.5em!important;
			color: #FFF!important;
			text-transform: lowercase!important;
		}
	}

	.toolbar-menu-button-mobile {
		background-color: transparent!important;
		color: #666!important;
	}
}

.twitter-button {
	position: fixed;
	bottom: 20px;
	right: 20px;
	width: 100px!important;
	max-width: 100px!important;
	h1 {
		font-size: 1.5em!important;
		color: #FFF!important;
		text-transform: lowercase!important;
		margin-bottom: 0px;
	}
}

.footer {
	pointer-events: none;
	background-color: transparent!important;
	min-height: 45px!important;
	height: 61px!important;
	padding-right: 6px;
	.footer-twiter-button {
		pointer-events: auto;
		background-color: transparent!important;
		h1 {
			font-size: 1.5em!important;
			color: #FFF!important;
			text-transform: lowercase!important;
			margin-bottom: 0px;
		}
	}
}

@media screen and (max-width: 600px) {
	.toolbar {
		padding-top: 10px;
	}
	.toolbar-title-button {
		width: 70px!important;
		min-width: 70px!important;
		margin-left: 10px!important;
	}
}
</style>



