import Vue from 'vue'
import Vuex from 'vuex'

import Events from './modules/events'
import Modals from './modules/modals'
import User from './modules/user'

Vue.use(Vuex)

const createStore = new Vuex.Store({
	modules: {
		Events,
		Modals,
		User
	},
	strict: false
})

export default createStore



