<template>
	<v-dialog transition="dialog-bottom-transition" fullscreen scrollable v-model="open">
		<v-card>
			<div>
				<v-btn @click="open = false" flat class="close-button ma-3"><i style="font-size: 2.4em; color: #888;" class="fal fa-times"></i></v-btn>
			</div>
			<div class="modal-content pa-3">
				<div class="modal-text-container">
					<h1 class="text-xs-center">Share your party checklist</h1>
					<p class="text-xs-center mb-4">Your friends will check off the items that they're bringing</p>
					<p class="text-xs-center">You can come back to this link to view and edit the updated checklist</p>
				</div>
				<div class="share-buttons mb-3">
					<v-btn flat class="share-button" @click="share('facebook')"><i style="color: #6689F3;" class="fab fa-facebook-square share-icon"></i></v-btn>
					<v-btn flat class="share-button" @click="share('twitter')"><i style="color: #6689F3;" class="fab fa-twitter-square share-icon"></i></v-btn>
					<v-btn flat class="share-button" @click="share('copy')"><i style="color: #6689F3;" class="fas fa-copy share-icon"></i></v-btn>
					<v-btn flat class="share-button" @click="share('email')"><i style="color: #6689F3;" class="fas fa-envelope share-icon"></i></v-btn>
				</div>
			</div>
		</v-card>
	</v-dialog>
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
export default {
	data () {
		return {
		}
	},
	methods: {
		...mapActions(['setModalOpen']),
		share (method) {
			console.log(method)
		}
	},
	computed: {
		...mapGetters(['getModalOpen']),
		open: {
			get () {
				return this.getModalOpen('sharePartyModal')
			},
			set (value) {
				this.setModalOpen({prop: 'sharePartyModal', value: value})
			}
		}
	}
}
</script>

<style lang="scss">
.close-button {
	width: 35px!important;
	min-width: 35px!important;
	height: 35px!important;
	min-height: 35px!important;
}

.modal-content {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 100%;
	h1 {
		margin-bottom: 10vh;
		font-size: 2.0em;
		color: #4A4A4A;
	}

	p {
		font-size: 1.1em;
		width: 70%;
		margin-left: 15%;
		color: #666;
	}
}

.share-buttons {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}

.modal-text-container {
	margin-top: 5vh;
}
</style>



