import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/pages/Home'
import CreateParty from '@/pages/CreateParty'
import ViewParty from '@/pages/ViewParty'

Vue.use(Router)

export default new Router({
	base: '/',
	mode: 'history',
	history: true,
	routes: [
		{
			path: '/',
			name: 'Home',
			component: Home,
			children: [
				{
					path: '/',
					name: 'CreateParty',
					component: CreateParty
				},
				{
					path: '/view-party',
					name: 'ViewParty',
					component: ViewParty
				}
			]
		}
	]
})



